/* Footer.css */
.footer {
    background-color: #2d2d2d;
    color: #f5f5f5;
    width: 100%;
    bottom: 0;
    left: 0;
    font-size: 0.9em;
}

.footer h4 {
    margin: 0;
    font-size: 1.2em;
    font-weight: bold;
}

.footer p {
    margin: 0.5em 0 0;
    font-size: 0.9em;
}